import { Input, Item } from './'
import React, { useEffect, useState } from 'react'
import { defaultParser, suggest } from '../core'

import Autowhatever from 'react-autowhatever'
import { Icon } from './Icon'
import PropTypes from 'prop-types'
import defaultTheme from '../styles'
import themeable from 'react-themeable'
import { usePipe } from '../hooks'

const renderInnerComponents = (input, icon) => {
  return <></>
}

const Search = (props) => {
  const {
    id,
    as,
    text,
    icon,
    placeholder,
    theme,
    //parser
    parser,
    parserOptions,
    //suggestions
    suggestions,
    suggestionAs,
    //events
    onSearch,
    onChange,
    onTextChange,
    onSelect,
  } = props

  const [active, setActive] = useState(false)
  const [state, setState] = useState({
    text: text,
    parsed: [],
    search: [],
    tokens: [],
    suggestions: [],
  })

  const context = { parserOptions, suggestions }
  const _write = usePipe(parser, suggest)
  const write = (...data) => _write.call(context, ...data)

  //Process search pipeline
  const processSearch = (text) => {
    let { next } = write({ prev: state, next: { text } })
    let event = { parsed: next.parsed, text: next.text }
    setState(next)
    onChange?.call(null, event)
  }

  //Event handlers
  const _onTextChange = onTextChange || processSearch
  const _onBlur = () => setActive(false)
  const _onFocus = () => setActive(true)
  const _onEnter = () => onSearch?.call(null, state)
  const _onItemMouseDown = (_, { itemIndex }) =>
    onSelect?.call(null, state.suggestions[itemIndex])

  //Controlled: process passed text
  useEffect(() => {
    if (typeof text !== 'undefined') {
      processSearch(text)
    }
  }, [text])

  const iconProps = {
    ...themeable(theme)(`${id}-icon`, 'icon'),
  }
  //Compose auto-whatever
  let itemComponent = (suggestion) =>
    React.createElement(suggestionAs, suggestion)

  var inputComponents = (props) => {
    return React.createElement(
      React.Fragment,
      null,
      React.createElement(as, props),
      React.createElement(Icon, iconProps)
    )
  }
  const items = active ? state.suggestions : []

  return (
    <Autowhatever
      id={id}
      items={items}
      theme={theme}
      renderItem={itemComponent}
      renderInputComponent={inputComponents}
      itemProps={{ onMouseDown: _onItemMouseDown }}
      inputProps={{
        placeholder,
        parsed: state.parsed,
        text: state.text,
        tokens: state.tokens,
        onFocus: _onFocus,
        onBlur: _onBlur,
        onTextChange: _onTextChange,
        onEnter: _onEnter,
      }}
    />
  )
}

Search.propTypes = {
  id: PropTypes.string,
  as: PropTypes.element,
  text: PropTypes.string,
  icon: PropTypes.element,
  placeholder: PropTypes.string,
  theme: PropTypes.oneOfType([PropTypes.array, PropTypes.object]),
  parser: PropTypes.func,
  parserOptions: PropTypes.object,
  suggestions: PropTypes.array,
  suggestionAs: PropTypes.element,
  onSearch: PropTypes.func,
  onChange: PropTypes.func,
  onTextChange: PropTypes.func,
  onSelect: PropTypes.func,
}

Search.defaultProps = {
  id: 'react-search-operators',
  as: Input,
  placeholder: 'Search...',
  suggestions: [],
  suggestionAs: Item,
  text: null,
  onSearch: null,
  onChange: null,
  onTextChange: null,
  onSelect: null,
  parserOptions: {},
  parser: defaultParser,
  theme: defaultTheme,
}

export default Search
