import React from 'react'

//Default input item
export const Input = React.forwardRef(
  ({ onTextChange, onEnter, text, ...props }, ref) => {
    return (
      <input
        ref={ref}
        {...props}
        onKeyUp={(e) => e.keyCode === 13 && onEnter()}
        onChange={(e) => onTextChange(e.target.value)}
        value={text}
      />
    )
  }
)
